<template>
  <CHeader fixed with-subheader light>
    <CToggler in-header class="ml-3 d-lg-none" @click="$store.commit('toggleSidebarMobile')" />
    <CToggler in-header class="ml-3 d-md-down-none" @click="$store.commit('toggleSidebarDesktop')" />
    <h4 class="mt-4">{{route}}</h4>
    <CHeaderNav class="ml-auto mb-3">
      <CButton class="mt-3 mr-3" v-if="isLoggedIn" @click="logout()" color="primary">
        <CIcon name="cil-account-logout" />&nbsp;Logout
      </CButton>
    </CHeaderNav>
  </CHeader>
</template>

<script>

export default {
  name: "TheHeader",
  data() {
    return {
      route: ""
    };
  },
  computed: {
    isLoggedIn: function() {
      return this.$store.getters.isLoggedIn;
    }
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        this.route = to.name;
      }
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("logout")
    }
  }
};
</script>
<style scoped>
.position {
  right: 50%;
  position: absolute;
}
</style>
